import React from 'react';

import axios from 'axios';

import Aux from '../../hoc/Aux';
import SectionHeader from '../Partials/SectionHeader';
import TextField from '../FormFields/TextField';
import RadioInput from '../FormFields/RadioInput';
import SelectInput from '../FormFields/SelectInput';

export default class RequestLoan extends React.Component {
  state = {};

  handleInputChange = e => {
    const name = e.target.name;
    const value = e.target.value;

    this.setState({ [name]: value });
  };

  componentDidMount() {
    axios
      .get('/members/guarantors')
      .then(response => {
        this.setState({ guarantors: response.data.data });
      })
      .catch(error => console.error(error));
  }

  handleSubmit = e => {
    e.preventDefault();
    const data = {
      ...this.state
    };
    axios
      .post('/loans/request', data)
      .then(response => {
        this.props.history.push('/loans/requested');
      })
      .catch(error => console.error(error));
  };

  render() {
    return (
      <Aux>
        <SectionHeader title="Request Loan" first="Loans" last="Request Loan" />
        <section className="content">
          <div className="row">
            <div style={{ padding: '60px' }}>
              <div className="box box-info">
                <div className="box-header with-border">
                  <h3 className="box-title">
                    Please fill in the information below
                  </h3>
                </div>
                <form className="form-horizontal" onSubmit={this.handleSubmit}>
                  <div className="box-body">
                    <TextField
                      id="memberID"
                      label="Member ID"
                      change={this.handleInputChange}
                      name="memberID"
                      placeholder="Member ID"
                      type="text"
                    />
                    <TextField
                      id="surname"
                      label="Surname"
                      change={this.handleInputChange}
                      name="surname"
                      placeholder="Surname"
                      type="text"
                    />
                    <TextField
                      id="otherNames"
                      label="Other Names"
                      change={this.handleInputChange}
                      name="otherNames"
                      placeholder="Other Names"
                      type="text"
                    />
                    <TextField
                      id="email"
                      label="E-mail"
                      name="email"
                      change={this.handleInputChange}
                      placeholder="E-mail"
                      type="email"
                    />
                    <TextField
                      id="phone"
                      label="Phone Number"
                      name="phone"
                      change={this.handleInputChange}
                      placeholder="Phone Number"
                      type="text"
                    />
                    <RadioInput
                      label="Gender"
                      name="gender"
                      change={this.handleInputChange}
                      inputs={[
                        { label: 'Male', value: 'male' },
                        { label: 'Female', value: 'female' }
                      ]}
                    />
                    <br />
                    <TextField
                      id="amount"
                      label="Amount"
                      change={this.handleInputChange}
                      name="amount"
                      type="text"
                      placeholder="Amount"
                    />
                    <SelectInput
                      label="Loan Type"
                      name="loanType"
                      options={[
                        { name: 'Simple', value: 'Simple' },
                        { name: 'Emergency', value: 'Emergency' },
                        { name: 'Complex', value: 'Complex' }
                      ]}
                      change={this.handleInputChange}
                    />
                    <SelectInput
                      label="Guarantor 1"
                      name="guarantor1"
                      options={this.state.guarantors}
                      change={this.handleInputChange}
                    />
                    <SelectInput
                      label="Guarantor 2"
                      name="guarantor2"
                      options={this.state.guarantors}
                      change={this.handleInputChange}
                    />
                    <div className="box-footer">
                      <button type="submit" className="btn btn-info pull-right">
                        Request Loan
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </section>
      </Aux>
    );
  }
}
